    date: '2024-12-08T15:30:00',
    summary: 'ÂêÑÈ°πÁõÆÁªÑÊ±áÊä•‰∫ÜÊúÄÊñ∞ËøõÂ±ïÔºåËÆ®ËÆ∫‰∫Ü‰∏ã‰∏ÄÊ≠•ÂºÄÂèëËÆ°ÂàíÂíåËµÑÊ∫êÈÖçÁΩÆÈúÄÊ±Ç„ÄÇ',
    status: 'completed',
